<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="(image,index) in images" :key=index>
            <img :src="image.tag" />
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "ImageGallery",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      images: [
        { tag: "http://fakeimg.pl/300/" },
        { tag: "http://fakeimg.pl/250x100/" },
        { tag: "http://fakeimg.pl/250x100/ff0000/" },
        { tag: "http://fakeimg.pl/350x200/ff0000/000" },
        { tag: "http://fakeimg.pl/350x200/ff0000,128/000,255" },
        { tag: "http://fakeimg.pl/350x200/?text=Hello" },
        { tag: "http://fakeimg.pl/350x200/?text=World&font=lobster" }
      ],
      swiperOption: {
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 4000,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        mousewheel: true,
        keyboard: {
          enabled: true
        },
        loop: true,
        slidesPerView: 1
      }
    };
  }
};
</script>

<style>
.active {
  display: block;
}

.inactive {
  display: none;
}
</style>



