<template>
  <div>
    <Header />
    <div class="about">
      <div class="name">
        {{name}}
      </div>
      <AboutMe />
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import AboutMe from './AboutMe'

export default {
  name: "About",
  components: {
    Header,
    AboutMe
  },
  data() {
    return {
      name: "Carmen Sanchez Luque",
      address: "MACLUQUE School of English. Tesoro, 14 - Malasaña"
    };
  }
};

</script>

<style>
@font-face {
  font-family: "Signature of the Ancient";
  src: url("../assets/Signature_of_the_Ancient.woff");
  font-weight: bold;

}
.name {
  font-family: "Signature of the Ancient";
  font-size: xx-large;
}
</style>



