<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="(timeshot,index) in timeline" :key=index>
            <div>
              <p>
                {{timeshot.text}}

              </p>
            </div>
            <div class="image-container">
              <img class="slides" :src="timeshot.tag" />
            </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "AboutMe",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      timeline: [
        { text: "En 1989 comencé mis estudios de Filología Inglesa en la Universidad Autónoma de Madrid. Me licencié en 1993.", tag: require("@/assets/graduation.gif") },
        { text: "Realizo trabajos de traducción escrita y de conferencias. He corregido las pruebas de imprenta de la European Journal of Psychological Assessment durante 7 años..", tag: require("@/assets/correctexams.gif") },
        { text: "He estado 11 años en el hospital Gregorio Marañón dando clase tanto a médicos como a técnicos.", tag: require("@/assets/doctor.gif") },
        { text: "Dentro del Turismo y la Hostelería he impartido cursos de Guía de Ruta, Informador Turístico, Recepcionista de Hotel, Atención al Público, Empleado Agencia de Viajes, Azafata de Congresos, Camarera de Piso, Cocinero, Gobernanta ...", tag: require("@/assets/chef.gif") },
        { text: "Relacionado con el mundo de los negocios (Business English): Inglés Financiero, Inglés Comercial, Lengua Extranjera Profesional para la Gestión Administrativa en la Relación con el Cliente, Interacciones Orales en el Entorno Empresarial ...", tag: require("@/assets/business.gif") },
        { text: "E incluso he impartido un curso de Inglés Técnico Aeronáutico además de innumerables cursos de inglés básico, medio y avanzado.", tag: require("@/assets/astronaut.gif") },
        { text: "Las necesidades de mis alumnos me han animado a escribir numerosos libros para su uso en el aula. He desarrollado varios escritos con explicaciones gramaticales dirigidas a diferentes niveles de aprendizaje, y otros libros con un nivel enfocado a cada alumno.", tag: require("@/assets/writer.gif"), width: "200px", height:"200px" },
        { text: "Pero mi gran pasión es la enseñanza. Llevo dedicándome a impartir clases de inglés desde que comencé mis estudios universitarios. La docencia me aporta una gran satisfacción personal y profesional.", tag: require("@/assets/teacher.gif") },
        { text: "Llevo más de 25 años convirtiendo mi vocación en mi profesión: la enseñanza.", tag: require("@/assets/teacher.png") },
        { text: "Esta gran dedicación hace que mis alumnos, aprendan disfrutando en mis clases. Se pierde el miedo al inglés.", tag: require("@/assets/nofear.gif") },
        { text: "En 2013 y gracias a la ayuda de mis hermanos nació Macluque School of English. Para estos tres cursos ya he elaborado más de 25 libros con un equilibrio perfecto entre use of English, reading, writing, listening & speaking. Están diseñados para superar los exámenes de Cambridge.", tag: require("@/assets/macluque.jpg") },
      ],
      swiperOption: {
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 6000,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        mousewheel: true,
        keyboard: {
          enabled: true
        },
        loop: true,
        slidesPerView: 1
      }
    };
  }
};
</script>

<style>
.image-container {
  width:40%;
  height: 40%;
  margin: 0 auto 0 auto;
}
img.slides {
  width:70%;
  height:70%;
}

p {
  font-family: "Engagement";
  font-size: 2.5rem
}

@font-face {
  font-family: "Engagement";
  src: url("../assets/Engagement-Regular.ttf");
}
</style>



