<template>
<div class="container">
  <div class="title">
      <img class="logo" src="../assets/macluque-logo.jpg" />
      <h1>{{ Title }}</h1>
  </div>
  <div class="links" >
    <router-link to="/">Main</router-link>
    <router-link to="/about">About</router-link>
  </div>
</div>
</template>

<script>
  export default {
    name: 'Header',
    data () {
      return {
        Title: 'LEARNING NOT STUDYING'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #0000ff;
}

h1 {
  padding-top: 2rem;
  width: 50%;
  margin-left:auto;
  margin-right:auto;
  position:relative;
}

img.logo {
  width:72px;
  height:72px;
  position:relative;
  float:left;
  margin-left: 20%;
  margin-top:1rem;
}
.container {
  background-color: white;
}

.links{
  margin-bottom: 3rem;
}
</style>



